import React from 'react';
import s from './Paginator.module.scss';

const Pagination = ({currentPage, totalUsersCount, setCurrentPage, pageSize}) => {

    const width = 2;
    const last = Math.ceil(totalUsersCount / pageSize);
    const left = currentPage - width;
    const right = currentPage + width + 1;
    const range = [];
    const rangeWithDots = [];
    let l;

    for (let i = 1; i <= last; i += 1) {
        if (i === 1 || i === last || (i >= left && i <= right)) {
            range.push(i);
        } else if (i < left) {
            i = left - 1;
        } else if (i > right) {
            range.push(last);
            break;
        }
    }

    range.forEach(i => {
        if (l) {
            if (i - l === 2) {
                rangeWithDots.push(l + 1);
            } else if (i - l !== 1) {
                rangeWithDots.push('...');
            }
        }
        rangeWithDots.push(i);
        l = i;
    });

    return (
        <div className={s.Pagination}>
            <button
                key={'prev'}
                className={currentPage === 1 ? s.Item + ' ' + s.Disabled : s.Item}
                disabled={currentPage === 1 ? 'disabled' : ''}
                onClick={() => setCurrentPage(currentPage - 1)}
            >
                {'<'}
            </button>

            {rangeWithDots.map((p, i) => {
                if (!isNaN(p)) {
                    return (
                        <button
                            key={p + i}
                            className={p === currentPage ? s.Item + ' ' + s.Active : s.Item}
                            disabled={p === currentPage ? 'disabled' : ''}
                            onClick={() => setCurrentPage(p)}
                        >
                            {p}
                        </button>
                    );
                } else {
                    return <div key={p + i} className={s.Dots}>...</div>;
                }
            })}

            <button
                key={'next'}
                className={currentPage === last ? s.Item + ' ' + s.Disabled : s.Item}
                disabled={currentPage === last ? 'disabled' : ''}
                onClick={() => setCurrentPage(currentPage + 1)}
            >
                {'>'}
            </button>
        </div>
    );
};

export default Pagination;