import React from 'react';
import s from './User.module.scss';
import {NavLink} from 'react-router-dom';
import defaultUserPic from '../../../assets/images/unnamed.jpg';

function User({user, isFollowingInProgress, toggleFollow}) {


    const userPic = user.photos.small ? user.photos.small : defaultUserPic;
    const following = user.followed ? 'Unfollow' : 'Follow';


    return (
        <div className={s.user}>
            <div className={s.action}>
                <NavLink to={`/profile/${user.id}`}>
                    <img className={s.pic} src={userPic} alt=""/>
                </NavLink>
                <button
                    disabled={isFollowingInProgress.some(id => id === user.id)}
                    className="btn"
                    onClick={() => toggleFollow(user.id, user.followed)}
                >{following}</button>
            </div>
            <div className={s.info}>
                <div>
                    <h3>{user.name}</h3>
                    <p>{user.status}</p>
                    <br/>
                </div>
            </div>
        </div>
    );
}

export default User;